* {
  margin: 0;
  padding: 0;
}

.container-fluid  {
  display: flex;
  position: fixed;
  align-content: stretch;
  background-color: #0e002f;
  z-index: 2;
  box-shadow: 0px 0px 50px rgb(9, 22, 21);
  font-family: 'Oswald', sans-serif;
  padding: 1%;
}
header #github {
  padding-left: 5%;
}
header #instagram {
  padding-left: 27%;
}

.link {
  text-decoration: none;
  color: white;
}
.link:hover {
  color:  rgb(7, 32, 29);
}

.header-github {
  width: 15%;
}

.header-instagram {
  width: 3.5%;
}

.header-linkedin {
  width: 19%;
}


aside {
  display: flex;
  position: absolute;
  background-color: #0e002f;
  position: fixed;
  height: 40rem;
  width: 10%;
  z-index: 1;
  color: white;
  flex-direction: column;
  padding-top: 3.8%;
  align-items: center;
  box-shadow: 0 0 50px rgb(9, 22, 21);
}
aside p {
  display: inline-block;
  padding: 5%;
  font-family: 'Oswald', sans-serif;
}
aside hr {
  display: inline;
  border: 1px solid;
  border-color: black;
}

aside p:hover {
  color: rgb(7, 32, 29);
}

h1 {
  padding: 12%;
  text-align: center;
  text-overflow:ellipsis;
  font-weight: bolder;
}
main {
  position: relative;
  background-color: #1a013d;
  color: rgb(192, 192, 192);
  height: 40rem;
  z-index: 0;
}
#first-h1 {
  font-family: 'Anton', sans-serif;
  text-align: left;
}
.line {
  width: 100%;
  height: 1px;
  background-color: rgb(153, 153, 153);
  margin-bottom: 15px;
}



@media screen and (max-width: 450px) {

  h1 {
     padding-top: 5em;
     padding-left: 2.5em;
  }
  #first-h1 {
      font-size: 1.5em;
  }

  .link {
   font-size: 0.8em;
  }

  aside {
      padding-top: 20%;
      height: 100%;
  }

aside p {
   font-size: 0.8em;
}

main {
   height: 100vh;
}

.header-img {
   width: 1em;
}
}